import store from '@/store' //token
import interfaceurl from './interface.js'

/**
 * api
 */
const req = {
    /**
     * 
     * @param {number} status
     */
    isHttpSuccess(status) {
        return (status >= 200 && status < 300) || status === 304;
    },
    /**
     * 
     * err
     * [string]errMsg
     */
    errPicker(err) {
        if (typeof err === 'string') {
            return err;
        }
        return err.msg || (err.errMsg.indexOf('request:fail') == 0 ? '' : err.errMsg) || (err.detail && err.detail.errMsg) || '';
    },
    /**
     * promise
     * @param {object} options {}
     */
    request(options = {}, keepLogin = true) {
		//token
		//let token = uni.getStorageSync('token')
		let token = store.state.token
		const {
			success,
			fail,
		} = options;
		// header
		const header = Object.assign({
			'content-type': 'application/json',
			//(token)token(Authorization)
			token: keepLogin ? token : ''
		}, options.header);
		return new Promise((res, rej) => {
			uni.request(Object.assign({},
				options, {
					header,
					success(r) {
						const isSuccess = req.isHttpSuccess(r.statusCode);
						if (isSuccess) { // 
							if (success) {
								success(r.data);
								return;
							}
							res(r.data);
						} else {
							const err = {
								msg: `~${r.statusCode}`,
								detail: r,
							};
							if (fail) {
								fail(err);
								return;
							}
							rej(err);
						}
					},
					fail(err) {
						if (fail) {
							fail(err);
							return;
						}
						rej(err);
					},
				},
			));
		});
    }
};

export default req;